import React from 'react';
import { Link } from 'react-router-dom';
import * as dayjs from 'dayjs';
import { TableCell, TableBody, TableRow } from '@windmill/react-ui';

import Status from '../table/Status';
import { FiFileText } from 'react-icons/fi';
import Tooltip from '../tooltip/Tooltip';
import SelectStatus from '../form/SelectStatus';

const OrderTable = ({ orders }) => {
    return (
        <>
            <TableBody>
                {orders?.map((order, i) => (
                    <TableRow key={i + 1}>
                        <TableCell>
                            <span className="text-sm">
                                {dayjs(order.from_date).format('MMM D, YYYY')}
                            </span>
                        </TableCell>
                        <TableCell>
                            <span className="text-sm">
                                {dayjs(order.to_date).format('MMM D, YYYY')}
                            </span>
                        </TableCell>

                        <TableCell>
                            {order.registered_user ? <>
                                <div className="text-sm">
                                    {order.user_id.address[0].street},{order.user_id.address[0].houseno},
                                    {order.user_id.address[0].town},{order.user_id.address[0].pincode}
                                </div>
                                <div className="text-sm">{order.user_id.address[0].district},{order.user_id.address[0].state}</div>
                                <div className="text-sm">{order.user_id.address[0].country}</div>
                            </> :
                                <span className="text-sm">{order.customerDetails.address.district}</span>}
                        </TableCell>
                        <TableCell>
                            <div>{order.registered_user ? order.user_id.fullname : order.customerDetails.name}</div>
                            {' '}
                            <div className="text-sm">{order.registered_user ? order.user_id.email : order.customerDetails.email}</div>{' '}
                            <div className="text-sm">{order.registered_user ? order.user_id.phoneno : order.customerDetails.phoneno}</div>{' '}
                        </TableCell>
                        <TableCell>
                            <span className="text-sm font-semibold">
                                {order.payment_method}
                            </span>
                        </TableCell>
                        <TableCell>
                            {' '}
                            <span className="text-sm font-semibold"> Per Day
                                &#8377; {Math.round(order.per_day_rent)}.00
                            </span>{' '}
                            <div>
                                &#8377; {((new Date(order.to_date).getTime() - new Date(order.from_date).getTime()) / (1000 * 60 * 60 * 24)) * Math.round(order.per_day_rent)}
                            </div>
                        </TableCell>
                        <TableCell className="text-center text-xs">
                            {order.payment_status}
                            {/* <Status status={order.payment_status} /> */}
                        </TableCell>
                        <TableCell className="text-center">
                            <SelectStatus id={order._id} />
                        </TableCell>
                        <TableCell className="text-right flex justify-end">
                            <div className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                                <Tooltip
                                    id="view"
                                    Icon={FiFileText}
                                    title="View Invoice"
                                    bgColor="#34D399"
                                />Download
                            </div>
                            {/* <div className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                                {' '}
                                <Link to={`/order/${order._id}`}>
                                    <Tooltip
                                        id="view"
                                        Icon={FiEye}
                                        title="View Invoice"
                                        bgColor="#34D399"
                                    />
                                </Link>
                            </div> */}
                        </TableCell>
                    </TableRow>
                ))}
            </TableBody>
        </>
    );
};

export default OrderTable;
